import { Injectable } from '@angular/core';
import { AlertController, ToastController, Alert } from 'ionic-angular';

@Injectable()
export class DialogService {
    constructor(
        private alertCtrl: AlertController,
        private toastCtrl: ToastController
    ) {
    }

    /**
     * 
     * @param msg 
     * @param duration 
     */
    tips(msg: string, duration = 3000) {
        let toast = this.toastCtrl.create({
            message: msg,
            cssClass:'text-center',
            duration: duration,
            position: 'middle'
        });
        toast.present();
    }
       /**
     * 
     * @param msg 
     * @param okText 
     * @param okHandler 
     * @param cancelText 
     * @param cancelHandler 
     * @param title title
     */
    onlyOk(msg: string, okText?: string, okHandler?: any, title?:string) {
        let alert = this.alertCtrl.create({
            title:title?title:'',
            cssClass:'alert-confirm',
            message: `<h6>${msg}</h6>`,
            buttons: [
                {
                    text: okText ? okText : '',
                    handler: okHandler
                }
            ],
            enableBackdropDismiss: false
        });
        alert.present();
    }
    /**
     * 
     * @param msg 
     * @param okText 
     * @param okHandler 
     * @param cancelText 
     * @param cancelHandler 
     * @param title title
     */
    confirm(msg: string, okText?: string, cancelText?: string, okHandler?: any, cancelHandler?: any, title?: string) {
        let alert = this.alertCtrl.create({
            title: title ? title : '',
            cssClass: 'alert-confirm',
            message: `<h6>${msg}</h6>`,
            buttons: [
                {
                    text: cancelText ? cancelText : '',
                    handler: cancelHandler
                },
                {
                    text: okText ? okText : '',
                    handler: okHandler
                }
            ],
            enableBackdropDismiss: false
        });
        alert.present();
    }
    /**
     * 
     * @param list 
     * @param okText 
     * @param cancelText 
     * @param okHandler 
     * @param title  
     */
    radio(list: Array<any>, okText?: string, cancelText?: string, okHandler?: any, title?: any, css?: string) {
        let alert = this.alertCtrl.create({
            cssClass: css ? `dialog-radio ${css}` : 'dialog-radio'
        });
        alert.setTitle(title);
        list.forEach((data, index, array) => {
            data.checked = false;
            if (!index) {
                data.checked = true;
            }
            alert.addInput({
                type: 'radio',
                label: data.value,
                value: data.value,
                checked: data.checked
            });
        })

        alert.addButton(cancelText);
        alert.addButton({
            text: okText,
            handler: data => {
                okHandler(data)
            }
        });
        alert.present();
    }
}



